<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'" label="" prop="knowledgenumber" >
					<el-input v-model="ruleForm.knowledgenumber" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.knowledgenumber" label="" prop="knowledgenumber" >
					<el-input v-model="ruleForm.knowledgenumber" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="knowledgetitle" >
					<el-input v-model="ruleForm.knowledgetitle" placeholder="" clearable  :readonly="ro.knowledgetitle"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="knowledgetitle" >
					<el-input v-model="ruleForm.knowledgetitle" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.images" label="" prop="images" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.images?ruleForm.images:''"
						@change="imagesUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.images" label="" prop="images" >
					<img v-if="ruleForm.images.substring(0,4)=='http'&&ruleForm.images.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.images" width="100" height="100">
					<img v-else-if="ruleForm.images.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.images.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.images.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="knowledgecategory" >
					<el-select :disabled="ro.knowledgecategory" v-model="ruleForm.knowledgecategory" placeholder="" >
						<el-option
							v-for="(item,index) in knowledgecategoryOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="knowledgecategory" >
					<el-input v-model="ruleForm.knowledgecategory"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="knowledgeintroduction" >
					<el-input v-model="ruleForm.knowledgeintroduction" placeholder="" clearable  :readonly="ro.knowledgeintroduction"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="knowledgeintroduction" >
					<el-input v-model="ruleForm.knowledgeintroduction" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info'&& !ro.teachingvideo" label="" prop="teachingvideo" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="1"
						:type="2"
						:multiple="true"
						:fileUrls="ruleForm.teachingvideo?ruleForm.teachingvideo:''"
						@change="teachingvideoUploadChange"
					></file-upload>
				</el-form-item> 
				<el-form-item v-else-if="ruleForm.teachingvideo" label="" prop="teachingvideo" >
					<el-button class="viewBtn" type="text" size="small" @click="download($base.url+ruleForm.teachingvideo)">
						<span class="icon iconfont icon-chakan14"></span>
						
					</el-button>
				</el-form-item>
				<el-form-item v-else-if="!ruleForm.teachingvideo" label="" prop="teachingvideo" >
					<el-button class="unBtn" type="text" size="small">
						<span class="icon iconfont icon-xihuan"></span>
						
					</el-button>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="releasetime" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.releasetime" 
						type="datetime"
						:readonly="ro.releasetime"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.releasetime" label="" prop="releasetime" >
					<el-input v-model="ruleForm.releasetime" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="" prop="knowledgedetails" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.knowledgedetails" 
					class="editor"
					myQuillEditor="knowledgedetails"
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.knowledgedetails" label="" prop="knowledgedetails" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.knowledgedetails"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					knowledgenumber : false,
					knowledgetitle : false,
					images : false,
					knowledgecategory : false,
					knowledgeintroduction : false,
					teachingvideo : false,
					knowledgedetails : false,
					releasetime : false,
					thumbsupnum : false,
					crazilynum : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					totalscore : false,
					storeupnum : false,
				},
			
				ruleForm: {
					knowledgenumber: this.getUUID(),
					knowledgetitle: '',
					images: '',
					knowledgecategory: '',
					knowledgeintroduction: '',
					teachingvideo: '',
					knowledgedetails: '',
					releasetime: '',
					clicktime: '',
				},
				knowledgecategoryOptions: [],

				rules: {
					knowledgenumber: [
					],
					knowledgetitle: [
					],
					images: [
					],
					knowledgecategory: [
					],
					knowledgeintroduction: [
					],
					teachingvideo: [
					],
					knowledgedetails: [
					],
					releasetime: [
					],
					thumbsupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					crazilynum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					clicktime: [
					],
					clicknum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					totalscore: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.releasetime = this.getCurDateTime()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='knowledgenumber'){
							this.ruleForm.knowledgenumber = obj[o];
							this.ro.knowledgenumber = true;
							continue;
						}
						if(o=='knowledgetitle'){
							this.ruleForm.knowledgetitle = obj[o];
							this.ro.knowledgetitle = true;
							continue;
						}
						if(o=='images'){
							this.ruleForm.images = obj[o];
							this.ro.images = true;
							continue;
						}
						if(o=='knowledgecategory'){
							this.ruleForm.knowledgecategory = obj[o];
							this.ro.knowledgecategory = true;
							continue;
						}
						if(o=='knowledgeintroduction'){
							this.ruleForm.knowledgeintroduction = obj[o];
							this.ro.knowledgeintroduction = true;
							continue;
						}
						if(o=='teachingvideo'){
							this.ruleForm.teachingvideo = obj[o];
							this.ro.teachingvideo = true;
							continue;
						}
						if(o=='knowledgedetails'){
							this.ruleForm.knowledgedetails = obj[o];
							this.ro.knowledgedetails = true;
							continue;
						}
						if(o=='releasetime'){
							this.ruleForm.releasetime = obj[o];
							this.ro.releasetime = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='totalscore'){
							this.ruleForm.totalscore = obj[o];
							this.ro.totalscore = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/knowledgecategory/knowledgecategory`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.knowledgecategoryOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `exerciseknowledge/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.knowledgedetails = this.ruleForm.knowledgedetails.replace(reg,'../../../springbootdti6c7gx/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.knowledgenumber) {
						this.ruleForm.knowledgenumber = String(this.ruleForm.knowledgenumber)
					}
					if(this.ruleForm.images!=null) {
						this.ruleForm.images = this.ruleForm.images.replace(new RegExp(this.$base.url,"g"),"");
					}
					if(this.ruleForm.teachingvideo!=null) {
						this.ruleForm.teachingvideo = this.ruleForm.teachingvideo.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `exerciseknowledge/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.exerciseknowledgeCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.exerciseknowledgeCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			imagesUploadChange(fileUrls) {
				this.ruleForm.images = fileUrls;
			},
			teachingvideoUploadChange(fileUrls) {
				this.ruleForm.teachingvideo = fileUrls;
			},
		}
	};
</script>
