<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="useraccount" >
					<el-input v-model="ruleForm.useraccount" placeholder="" clearable  :readonly="ro.useraccount"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="useraccount" >
					<el-input v-model="ruleForm.useraccount" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="password" >
					<el-input v-model="ruleForm.password" placeholder="" clearable  :readonly="ro.password"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="password" >
					<el-input v-model="ruleForm.password" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="username" >
					<el-input v-model="ruleForm.username" placeholder="" clearable  :readonly="ro.username"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="username" >
					<el-input v-model="ruleForm.username" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.headportrait" label="" prop="headportrait" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.headportrait?ruleForm.headportrait:''"
						@change="headportraitUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.headportrait" label="" prop="headportrait" >
					<img v-if="ruleForm.headportrait.substring(0,4)=='http'&&ruleForm.headportrait.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.headportrait" width="100" height="100">
					<img v-else-if="ruleForm.headportrait.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.headportrait.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.headportrait.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="gender" >
					<el-select :disabled="ro.gender" v-model="ruleForm.gender" placeholder="" >
						<el-option
							v-for="(item,index) in genderOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="gender" >
					<el-input v-model="ruleForm.gender"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="age" >
					<el-input v-model.number="ruleForm.age" placeholder="" clearable  :readonly="ro.age"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="age" >
					<el-input v-model="ruleForm.age" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="height" >
					<el-input-number v-model="ruleForm.height" placeholder="" :disabled="ro.height"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="height" >
					<el-input v-model="ruleForm.height" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="weight" >
					<el-input-number v-model="ruleForm.weight" placeholder="" :disabled="ro.weight"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="weight" >
					<el-input v-model="ruleForm.weight" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="contactinformation" >
					<el-input v-model="ruleForm.contactinformation" placeholder="" clearable  :readonly="ro.contactinformation"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="contactinformation" >
					<el-input v-model="ruleForm.contactinformation" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="passwordwrongnum" >
					<el-input v-model.number="ruleForm.passwordwrongnum" placeholder="" clearable  :readonly="ro.passwordwrongnum"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="passwordwrongnum" >
					<el-input v-model="ruleForm.passwordwrongnum" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
		isIntNumer,
		isMobile,
	} from "@/utils/validate";
	export default {
		data() {
			var validateMobile = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isMobile(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					useraccount : false,
					password : false,
					username : false,
					headportrait : false,
					gender : false,
					age : false,
					height : false,
					weight : false,
					contactinformation : false,
					status : false,
					passwordwrongnum : false,
				},
			
				ruleForm: {
					useraccount: '',
					password: '',
					username: '',
					headportrait: '',
					gender: '',
					age: '',
					height: '',
					weight: '',
					contactinformation: '',
				},
				genderOptions: [],

				rules: {
					useraccount: [
						{ required: true, message: '', trigger: 'blur' },
					],
					password: [
						{ required: true, message: '', trigger: 'blur' },
					],
					username: [
						{ required: true, message: '', trigger: 'blur' },
					],
					headportrait: [
					],
					gender: [
					],
					age: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					height: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					weight: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					contactinformation: [
						{ validator: validateMobile, trigger: 'blur' },
					],
					status: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					passwordwrongnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='useraccount'){
							this.ruleForm.useraccount = obj[o];
							this.ro.useraccount = true;
							continue;
						}
						if(o=='password'){
							this.ruleForm.password = obj[o];
							this.ro.password = true;
							continue;
						}
						if(o=='username'){
							this.ruleForm.username = obj[o];
							this.ro.username = true;
							continue;
						}
						if(o=='headportrait'){
							this.ruleForm.headportrait = obj[o];
							this.ro.headportrait = true;
							continue;
						}
						if(o=='gender'){
							this.ruleForm.gender = obj[o];
							this.ro.gender = true;
							continue;
						}
						if(o=='age'){
							this.ruleForm.age = obj[o];
							this.ro.age = true;
							continue;
						}
						if(o=='height'){
							this.ruleForm.height = obj[o];
							this.ro.height = true;
							continue;
						}
						if(o=='weight'){
							this.ruleForm.weight = obj[o];
							this.ro.weight = true;
							continue;
						}
						if(o=='contactinformation'){
							this.ruleForm.contactinformation = obj[o];
							this.ro.contactinformation = true;
							continue;
						}
						if(o=='status'){
							this.ruleForm.status = obj[o];
							this.ro.status = true;
							continue;
						}
						if(o=='passwordwrongnum'){
							this.ruleForm.passwordwrongnum = obj[o];
							this.ro.passwordwrongnum = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.genderOptions = ",".split(',')
			
			},
			// 

			info(id) {
				this.$http({
					url: `user/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(((this.ruleForm.useraccount).toString().length<3)){
						this.$message.error(`3`);
						return
					}
					if(((this.ruleForm.useraccount).toString().length>16)){
						this.$message.error(`16`);
						return
					}
					if(((this.ruleForm.password).toString().length<3)){
						this.$message.error(`3`);
						return
					}
					if(((this.ruleForm.password).toString().length>16)){
						this.$message.error(`16`);
						return
					}
					if(this.ruleForm.headportrait!=null) {
						this.ruleForm.headportrait = this.ruleForm.headportrait.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `user/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.userCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.userCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			headportraitUploadChange(fileUrls) {
				this.ruleForm.headportrait = fileUrls;
			},
		}
	};
</script>
