<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'" label="" prop="activitynumber" >
					<el-input v-model="ruleForm.activitynumber" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.activitynumber" label="" prop="activitynumber" >
					<el-input v-model="ruleForm.activitynumber" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="activityname" >
					<el-input v-model="ruleForm.activityname" placeholder="" clearable  :readonly="ro.activityname"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="activityname" >
					<el-input v-model="ruleForm.activityname" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.activitypicture" label="" prop="activitypicture" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.activitypicture?ruleForm.activitypicture:''"
						@change="activitypictureUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.activitypicture" label="" prop="activitypicture" >
					<img v-if="ruleForm.activitypicture.substring(0,4)=='http'&&ruleForm.activitypicture.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.activitypicture" width="100" height="100">
					<img v-else-if="ruleForm.activitypicture.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.activitypicture.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.activitypicture.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="activitytype" >
					<el-select :disabled="ro.activitytype" v-model="ruleForm.activitytype" placeholder="" >
						<el-option
							v-for="(item,index) in activitytypeOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="activitytype" >
					<el-input v-model="ruleForm.activitytype"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="activitytime" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.activitytime" 
						type="datetime"
						:readonly="ro.activitytime"
						:picker-options="activitytimePickerOptions"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.activitytime" label="" prop="activitytime" >
					<el-input v-model="ruleForm.activitytime" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="activitylocation" >
					<el-input v-model="ruleForm.activitylocation" placeholder="" clearable  :readonly="ro.activitylocation"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="activitylocation" >
					<el-input v-model="ruleForm.activitylocation" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="activityquota" >
					<el-input v-model.number="ruleForm.activityquota" placeholder="" clearable  :readonly="ro.activityquota"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="activityquota" >
					<el-input v-model="ruleForm.activityquota" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="" prop="activityroute" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.activityroute" 
					class="editor"
					myQuillEditor="activityroute"
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.activityroute" label="" prop="activityroute" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.activityroute"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					activitynumber : false,
					activityname : false,
					activitypicture : false,
					activitytype : false,
					activitytime : false,
					activitylocation : false,
					activityquota : false,
					activityroute : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					totalscore : false,
					storeupnum : false,
				},
			
				ruleForm: {
					activitynumber: this.getUUID(),
					activityname: '',
					activitypicture: '',
					activitytype: '',
					activitytime: '',
					activitylocation: '',
					activityquota: Number('0'),
					activityroute: '',
					clicktime: '',
				},
				activitytypeOptions: [],

				activitytimePickerOptions: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				rules: {
					activitynumber: [
					],
					activityname: [
					],
					activitypicture: [
					],
					activitytype: [
					],
					activitytime: [
					],
					activitylocation: [
					],
					activityquota: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					activityroute: [
					],
					clicktime: [
					],
					clicknum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					totalscore: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='activitynumber'){
							this.ruleForm.activitynumber = obj[o];
							this.ro.activitynumber = true;
							continue;
						}
						if(o=='activityname'){
							this.ruleForm.activityname = obj[o];
							this.ro.activityname = true;
							continue;
						}
						if(o=='activitypicture'){
							this.ruleForm.activitypicture = obj[o];
							this.ro.activitypicture = true;
							continue;
						}
						if(o=='activitytype'){
							this.ruleForm.activitytype = obj[o];
							this.ro.activitytype = true;
							continue;
						}
						if(o=='activitytime'){
							this.ruleForm.activitytime = obj[o];
							this.ro.activitytime = true;
							continue;
						}
						if(o=='activitylocation'){
							this.ruleForm.activitylocation = obj[o];
							this.ro.activitylocation = true;
							continue;
						}
						if(o=='activityquota'){
							this.ruleForm.activityquota = obj[o];
							this.ro.activityquota = true;
							continue;
						}
						if(o=='activityroute'){
							this.ruleForm.activityroute = obj[o];
							this.ro.activityroute = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='totalscore'){
							this.ruleForm.totalscore = obj[o];
							this.ro.totalscore = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
					this.ruleForm.activityquota = Number('0'); 				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/activitytype/activitytype`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.activitytypeOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `walkingactivity/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.activityroute = this.ruleForm.activityroute.replace(reg,'../../../springbootdti6c7gx/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.activitynumber) {
						this.ruleForm.activitynumber = String(this.ruleForm.activitynumber)
					}
					if(this.ruleForm.activitypicture!=null) {
						this.ruleForm.activitypicture = this.ruleForm.activitypicture.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `walkingactivity/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.walkingactivityCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.walkingactivityCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			activitypictureUploadChange(fileUrls) {
				this.ruleForm.activitypicture = fileUrls;
			},
		}
	};
</script>
